@import url("https://fonts.googleapis.com/css2?family=Abril+Fatface&family=Gowun+Dodum&family=Roboto+Slab:wght@200;400;500&display=swap");

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
  transition: all 0.3s;
  width: 100%;
  height: 100vh;
  font-family: "Gowun Dodum", sans-serif;
  background-color: #fbf7f0;
  color: #6b5a59;
  font-size: 16px;
}

::-webkit-scrollbar {
  display: none;
}

.swiper {
  position: relative;
  width: 100%;
  height: 100vh;
  background-color: transparent;
}

.swiper-pagination-bullet {
  background-color: transparent !important;
  opacity: 1;
  padding: 15px 0px;
  position: relative;
  top: 0;
}

.swiper-pagination-bullets {
  font-family: "Roboto Slab", serif;
  font-size: 15px;
  width: 7%;
  height: 5vh;
  background-color: transparent;
  display: inline-table;
}

.swiper-pagination-bullet:hover {
  background: url(/public/img/star1.png) no-repeat 50% 50%;
  background-size: 10px 15px;
  transition: all 0.6s;
  color: #6b5a59;
  opacity: 1;
}

.Sbullet {
  position: relative;
  width: auto;
  top: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
  color: #6b5a59;
}

#wrap {
  width: 90%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 9999;
  filter: drop-shadow(0 0 10px rgba(0, 0, 0, 0.2));
  padding: 2%;
}

section {
  width: 100%;
  height: 100vh;
  overflow: hidden;
  border-bottom: 1px solid #6b5a59;
  box-sizing: border-box;
}

.title {
  text-align: center;
  font-size: 28px;
  padding: 0px 10px;
  font-weight: bold;
  margin: 35px auto;
  width: 200px;
  background: url(/public/img/star1.png) no-repeat left center,
    url(/public/img/star1.png) no-repeat right center;
  background-size: 20px 20px;
}

.backImg {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
}

/*****************************************
                  main
*****************************************/

#main {
  background-color: #eee2cb;
  position: relative;
}

#NavMain {
  position: relative;
  height: 12%;
  border-bottom: 1px solid #6b5a59;
}

#main nav {
  position: absolute;
  top: 50%;
  right: 10%;
  transform: translateY(-50%);
  font-family: "Roboto Slab", serif;
  display: none;
}

#main nav li {
  display: inline;
  margin-left: 20%;
  padding-top: 20%;
}

#main nav li:hover {
  background: url(/public/img/star1.png) no-repeat 50% 50%;
  background-size: 15px 15px;
  transition: all 0.6s;
  cursor: pointer;
}

#main h1 {
  font-size: 30px;
  text-transform: uppercase;
  font-family: "Abril Fatface", cursive;
  font-style: italic;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #6b5a59;
  background: url(/public/img/star1.png) no-repeat 0% 50%;
  background-size: 35px 35px;
  padding-left: 45px;
}

#main .mainTitle {
  width: 20%;
  position: absolute;
  left: 1%;
  top: 10%;
  animation: logo 20s linear infinite;
  z-index: 10000;
}

@keyframes logo {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

/*****************************************
                  about
*****************************************/

#about {
  background: #fbf7f0 url(/public/img/back1.png) no-repeat center center;
  height: 88%;
  position: relative;
}

#about .about-img {
  float: left;
  width: 45%;
  height: 100%;
  position: absolute;
  left: 0;
}

#about .about-img img {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

#about .about-text {
  float: right;
  font-size: 21px;
  width: 60%;
  height: 100%;
  position: relative;
}

#about div article {
  width: 100%;
  text-align: left;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#about div p {
  padding-top: 10px;
}

#about div ul {
  width: 60%;
  margin: 0;
  padding: 0;
}

#about div ul li {
  padding: 15px 0 15px 0;
}

#about div ul li:nth-of-type(1) {
  margin-top: 10px;
  border-top: 1px solid #6b5a59;
  border-bottom: 1px solid #6b5a59;
}

/*****************************************
                  design
*****************************************/

#design,
#designB,
#designC {
  background: #fbf7f0 url(/public/img/back1.png) no-repeat center center;
  position: relative;
}

#Adesign,
#Bdesign,
#Cdesign {
  width: 100%;
  height: 100%;
  position: relative;
  margin-top: 5%;
}

#Adesign img,
#Bdesign img,
#designC img {
  width: 100%;
  outline: 1px solid #6b5a59;
}

#Adesign ul,
#Bdesign ul {
  width: 87%;
  margin: 6.5% auto;
}

#Bdesign ul {
  position: relative;
  left: 50%;
  transform: translateX(-50%);
}
#design li,
#designB li {
  float: left;
  position: relative;
  cursor: pointer;
}

#design li {
  width: 22%;
  padding: 15px;
}

#designB li {
  width: 40%;
  padding: 15px;
}

#design li:after,
#designB li:after {
  display: block;
  opacity: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "크게 보기";
  font-size: 12px;
  padding: 10px 12px;
  background-color: #6b5a59;
  color: #fbf7f0;
  border-radius: 50%;
  transition: all 0.5s;
  filter: drop-shadow(0px 5px 5px rgba(0, 0, 0, 0.3));
}

#design li:hover::after,
#designB li:hover::after {
  opacity: 1;
}

.ModalA,
.ModalB {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10000;
}

.ModalA button,
.ModalB button {
  padding: 10px 15px;
  border: 1px solid #6b5a59;
  background-color: #fbf7f0;
  color: #6b5a59;
  border-radius: 20px;
  font-weight: bold;
  font-size: 13px;
  position: absolute;
  top: 0.5%;
  right: 0.5%;
  cursor: pointer;
  z-index: 10000;
}

.ModalA img {
  width: 600px;
  border: 1px solid #6b5a59;
  filter: drop-shadow(0 0 20px rgba(0, 0, 0, 0.6));
}

.ModalB img {
  width: 1000px;
  border: 1px solid #6b5a59;
  filter: drop-shadow(0 0 20px rgba(0, 0, 0, 0.6));
}

#designC ul {
  width: 80%;
  margin: 7% auto;
}

#designC ul li {
  display: inline-block;
  position: relative;
  padding: 1%;
  width: 23%;
}

#designC a {
  display: block;
  position: relative;
  background-color: #eee2cb;
  margin-top: 7%;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 500px;
  width: 150px;
  padding: 1%;
  outline: 1px solid #6b5a59;
  transition: all 0.7s;
}

#designC a:hover {
  background-color: #f1f3f6;
}

#designD {
  background-color: #f5c04d;
}

#designD .leftSide {
  float: left;
  width: 50%;
  height: 100vh;
  background: url(/public/img/back2.png) no-repeat bottom/cover,
    url(/public/img/back3.png) no-repeat top/cover;
  background-size: 100%;
  border: 1px solid #6b5a59;
  box-sizing: border-box;
}

#BrandDesign {
  height: 58%;
  position: relative;
  top: 37%;
  transform: translateY(-40%);
}

#BrandDesign .title {
  padding: 0 30px;
}

#BrandDesign a {
  padding: 10px 30px;
  background-color: #6b5a59;
  color: #fbf7f0;
  border-radius: 15px;
  position: absolute;
  top: 100%;
  left: 50%;
  transform: translate(-50%, -70%);
  transition: all 0.7s;
}

#BrandDesign a:hover {
  background-color: #eee2cb;
  color: #6b5a59;
  outline: 1px solid #6b5a59;
}

#BrandDesign img {
  width: 70%;
  position: absolute;
  top: 32%;
  left: 50%;
  transform: translate(-50%, -30%);
}

#designD .rightSide {
  width: 50%;
  height: 100vh;
  float: right;
  background: url(/public/img/text1.png) no-repeat center center,
    url(/public/img/pic1.jpg) no-repeat center center/cover;
}

/*****************************************
                  homepage
*****************************************/

#homepage {
  color: #6b5a59;
  background: #fbf7f0;
}

#homepage p {
  text-align: center;
  font-size: 28px;
  font-weight: bold;
  margin: 0% auto;
}

#homepage .grey p,
#homepage .painting p {
  width: 380px;
  height: 0px;
  background: url(/public/img/star2.png) no-repeat left bottom,
    url(/public/img/star2.png) no-repeat right bottom;
  background-size: 20px 20px;
  padding: 25% 0 8% 0;
}

#homepage .peripera p {
  width: 340px;
  height: 0;
  background: url(/public/img/star1.png) no-repeat left bottom,
    url(/public/img/star1.png) no-repeat right bottom;
  background-size: 20px 20px;
  padding: 20% 0 8% 0;
}

#homepage .painting p {
  padding: 20% 0 8% 0;
}

#homepage .DMain {
  padding-top: 6%;
}

#homepage span {
  margin: 0px auto;
  width: 300px;
  display: block;
  text-align: center;
}

#homepage .part {
  font-size: 14px;
  padding-bottom: 4%;
  margin: 5px auto;
  border-bottom: 1px solid #fbf7f0;
}

#homepage .peripera .part {
  border-bottom: 1px solid #6b5a59;
}

#homepage article {
  position: relative;
  height: 100vh;
}

#homepage .peripera .leftArt,
#homepage .grey .leftArt,
#homepage .painting .leftArt {
  width: 400px;
  position: relative;
  color: #fbf7f0;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 100%;
}

#homepage .peripera .leftArt {
  color: #6b5a59;
}

#homepage a {
  background-color: #eee2cb;
  color: #6b5a59;
  width: 160px;
  height: 45px;
  line-height: 2.7;
  display: inline-block;
  border: 1px solid #6b5a59;
  border-radius: 10px;
  margin-left: 20px;
  transition: all 0.3s;
}

#homepage a:hover {
  background-color: #f1f3f6;
}

#homepage img {
  width: 380px;
  margin: 57px 0;
  filter: drop-shadow(0 0 20px rgba(0, 0, 0, 0.2));
}

#homepage .peripera {
  background: url(/public/img/peripera1.png) no-repeat center center/cover;
}
#homepage .grey {
  background: url(/public/img/grey1.jpg) no-repeat center center/cover;
}

#homepage .painting {
  background: url(/public/img/painting1.jpg) no-repeat center center/cover;
}
